
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'edit',
props: ['id'],
data: function () {
return {
topic: {
title: '',
content: ''
}
};
},
methods: {

edittopic: function() {
var app = this;
this.$http.post('/topics/update', {
topic_id: this.topic.id,
title: this.topic.title,
content: this.topic.content,
accesstoken: this.$store.state.accesstoken,
tab: 'dev'
}).then(function (response) {


app.$router.back();
})
}
},
components: {
Group,
XTextarea,
XButton
},
beforeRouteEnter: function(to, from, next) {
next(function (vm) {
vm.$http.get('/topic/' + to.params.id).then(function (response) {
vm.topic = response.data.data;
vm.topic.content = response.data.data.content.replace(/<[^>]+>/g,"");
});
});
},
beforeRouteUpdate: function(to, from, next) {
var app = this;
this.$http.get('/topic/' + to.params.id).then(function (response) {
app.topic = response.data.data;
});
}
}



